*{
    margin:0;
}
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap');

body{
    background-color: #EFF6EE;
    font-family: 'Montserrat', sans-serif;
}
input{
    outline: none;
}
.form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    width:100%;
    height:300px;
}
.form-field{
    font-family: 'Montserrat', sans-serif;
    font-size:30px;
    color:black;
    border: 0px;
    border-bottom: 2px solid black;
    background-color: #EFF6EE ;
    margin: 20px 40px 50px 10px;
}
.calc{
    font-family: 'Montserrat', sans-serif;
    font-size:30px;
    color:black;
    border:2px solid black;
    padding:5px 30px;
    cursor: pointer;
}

.fields{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
}
.button{
    display:flex;
    justify-content: center;
}

.results{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top:70px;
}
.results p {
    font-size:30px;
    text-align: center;
}
.love-per{
    position: relative;
    text-align: center;
    color: white;
}
.love-per h2{
    position: absolute;
    font-size: 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.love-svg{
    width:200px;
}
.invisible{
    visibility: hidden;
}
@media only screen and (max-width:400px){
    .form-field{
        width:100%;
        font-size:25px;
    }
    .calc{
        font-size: 20px;
    }
}